.chatbot-container {
  max-width: 800px;
  margin: auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
}

.chat-messages {
  padding: 10px;
  max-height: 300px;
  overflow-y: auto;
 
}

.message-container {
  padding: 8px;
  margin: 5px 0;
  border-radius: 5px;
  position: relative;
}

.user-message {
  background-color: #e0f2fe;
  color: #000;
}

.bot-message {
  background-color: #f0f0f0;
  color: #000;
}

.button-container {
  display: flex;
  justify-content: center;
  margin-top: 5px;
}

.input-container {
  display: flex;
  align-items: center;
  justify-content: center; /* Add this line to center horizontally */
  padding: 10px;
  width: 80%; /* Adjust width as needed */
  margin-left: 60px;
}

.input-field {
  flex: 1; /* Change flex property to allow the input field to expand */
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}

.send-button {
  padding: 8px 16px;
  border: none;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  cursor: pointer;
}
